import React from 'react'
import { useParams } from 'react-router'
import { Instruments } from '../models/Instruments';

const InstrumentScreen = ({history}) => {
    const { id } = useParams();
    const { type, name, description} = Instruments.find(instrument => instrument.id === id)
    const path = `/assets/${type}-${id}.png`
    
    const handleBack = () => {
        history.goBack()
    }
    
    return (
        <div className="container row mt-5">
            <div className="col-8">
                <img className="img-thumbnail p-4" style={{
                    width: "25%",
                    height: "500px"
                }} src={path} alt={id} />
            </div>
            <div className="col-4">
                <h2>Nombre: {name}</h2>
                <p>Descripci√≥n: {description}</p>
                <button onClick={handleBack} className="btn btn-outline-warning">Go Back</button>
            </div>
        </div>
    )
}

export default InstrumentScreen
